import api from "@/common/index.js";
import over from '@/components/over.vue';
import imglazy from '@/components/img-lazy/img-lazy.vue';
import uniNumberBox from '@/components/uni-number-box.vue';
let mvc = {
	detailList: [{
		item_name: '',
		item_en: '',
		item_no: '',
		valid_price: '',
		real_qty: '',
		sub_amt: ''
	}],
	orderInfo: {
		sheet_no: 0,
		sheet_amt: 0,
		yhh_amt: 0,
	},
	sn: '',
	over: false
};
export default {
	components: {
		over,
		imglazy,
		uniNumberBox
	},
	data() {
		return {
			imglazyStyle: 'border-radius: 10upx;',
			mvc: {
				detailList: [{
					item_name: '',
					item_en: '',
					item_no: '',
					valid_price: '',
					real_qty: '',
					sub_amt: ''
				}],
				orderInfo: {
					sheet_no: 0,
					sheet_amt: 0,
					yhh_amt: 0,
				},
				sn: '',
				over: false,

			},
			giveList: [],
			getStatus: false,
			num: 0,
			total: 0,
		};
	},
	onLoad(option) {
		let that = this;
		setTimeout(function() {
			api.load(that.$t('index').loadmsg)
			api.pageTitle(that.$t('order_data').title)
			mvc.sn = option.sn;
			that.loadData();
		})
	},
	computed: {
		i18n() {
			return this.$t('order_data')
		}
	},
	onShow() {},
	onHide() {

	},
	onBackPress() {

	},


	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		loadData() {
			let that = this;
			mvc.over = false;
			let params = {
				sn: mvc.sn
			}
			that.$ajax.orderHaView(params).then(res => {
				mvc.detailList = res.list;
				mvc.orderInfo = res.info;
				for (let i in mvc.detailList) {
					mvc.detailList[i].url = that.$store.getters.imgShow + "/thumb/" + mvc.detailList[i].item_no +
						".jpg?x-oss-process=style/300";

				}
				mvc.over = true;
				uni.hideLoading();
				that.mvc = mvc;
			})
		},
		goods() {
			let params = {
				sheet_no: mvc.sn
			}
			this.$ajax.orders(params).then(res => {
				if (res.free) {
					let result = res.free.list
					for (let i in result) {
						result[i].url = this.$store.getters.imgShow + "/thumb/" + result[i].item_no +
							".jpg?x-oss-process=style/300";
						result[i].checked = false;
						result[i].allNum = 1;
						result[i].num = 1;
					}
					this.giveList = result;
					this.total = res.free.money
				}

			})

		},
		//详情页
		navToDetailPage(item) {
			uni.navigateTo({
				url: `/pages/product/product?productId=${item}`
			})
		},
		scroll(e) {
			console.log(e)

		},

		bindRadio(index) {
			this.giveList[index].checked = !this.giveList[index].checked;
			let items = this.giveList
			this.num = 0;
			items.forEach((item) => {
				if (item.checked) {
					console.log(item)
					this.num = this.num + (Number(item.sale_price) * Number(item.num))
				}
			})
			this.giveList = items;
			if (this.num > this.total) {
				uni.showToast({
					title: "仅限领取" + this.total + "元内",
					icon: 'none',
				});
			}
		},

		getOrder() {
			if (this.num > this.total) {
				uni.showToast({
					title: "仅限领取" + this.total + "元内",
					icon: 'none',
				});
				return false;
			}
			let list = this.giveList;
			let params = {
				parent_sheet: mvc.sn,
				items: [],
				qtys: []
			}
			list.forEach((item) => {
				if (item.checked) {
					params.items.push(item.item_no);
					params.qtys.push(item.num)
				}
			})
			this.$ajax.create_free_order(params).then(res => {
				if (res.data.res == "success") {
					uni.showToast({
						title: "领取成功",
						icon: 'none'
					});
				}else{
					uni.showToast({
						title: "重复领取失败",
						icon: 'none'
					});
				}

			})
		},
		bindChange(e) {

			this.giveList[e.index].num = e.number;
			let items = this.giveList
			this.num = 0;
			items.forEach((item) => {
				if (item.checked) {
					this.num = this.num + (Number(item.sale_price) * Number(item.num))
				}
			})
			if (this.num > this.total) {
				uni.showToast({
					title: "仅限领取" + this.total + "元内",
					icon: 'none',
				});
			}
		}
	},

}
